
.main_timeline{
    padding-top: 50px;
    overflow: hidden;
    position: relative;
  }
  .main_timeline:before{
    content: "";
    width: 7px;
    height: 100%;
    background: var(--bg-root-timeline);
    margin: 0 auto;
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
  }
  .main_timeline .timeline{
    width: 50%;
    float: left;
    padding: 20px 60px;
    border-top: 7px solid var(--bg-root-timeline);
    border-right: 7px solid var(--bg-root-timeline);
    border-radius: 0 30px 0 0;
    position: relative;
    right: -3.5px;
  }
  .main_timeline .icon{
    display: block;
    width: 50px;
    height: 50px;
    line-height:50px;
    border-radius: 50%;
    background: var(--bg-timeline);;
    border: 1px solid #fff;
    text-align: center;
    font-size: 25px;
    color: #fff;
    box-shadow: 0 0 0 2px var(--bg-timeline);;
    position: absolute;
    top: -30px;
    left: 0;
  }
  .main_timeline .timeline_content{
    display: block;
    padding: 30px 10px 10px;
    border-radius: 20px;
    background: var(--bg-timeline);;
    color: #fff;
    position: relative;
  }
  .main_timeline .timeline_content:hover{
    text-decoration: none;
    color: #fff;
  }
  .main_timeline .timeline_content:before,
  .main_timeline .timeline_content:after{
    content: "";
    display: block;
    width: 10px;
    height: 50px;
    border-radius: 10px;
    background: var(--bg-timeline);;
    border: 1px solid #fff;
    position: absolute;
    top: -35px;
    left: 50px;
  }
  .main_timeline .timeline_content:after{
    left: auto;
    right: 50px;
  }
  .main_timeline .title{
    font-size: 24px;
    margin: 0;
  }
  .main_timeline .description{
    font-size: 15px;
    letter-spacing: 1px;
    margin: 0 0 5px 0;
  }
  .main_timeline .timeline:nth-child(2n){
    border-right: none;
    border-left: 7px solid var(--bg-root-timeline);
    border-radius: 30px 0 0 0;
    right: auto;
    left: -3.5px;
  }
  .main_timeline .timeline:nth-child(2n) .icon{
    left: auto;
    right: 0;
  }
  .main_timeline .timeline:nth-child(2){ margin-top: 130px; }
  .main_timeline .timeline:nth-child(odd){ margin: -130px 0 30px 0; }
  .main_timeline .timeline:nth-child(even){ margin-bottom: 80px; }
  .main_timeline .timeline:first-child,
  .main_timeline .timeline:last-child:nth-child(even){ margin: 0 0 30px 0; }
  .main_timeline .timeline:nth-child(2n) .timeline_content,
  .main_timeline .timeline:nth-child(2n) .timeline_content:before,
  .main_timeline .timeline:nth-child(2n) .timeline_content:after,
  .main_timeline .timeline:nth-child(2n) .icon{ background: var(--bg-timeline); }
  .main_timeline .timeline:nth-child(2n) .icon{ box-shadow: 0 0 0 2px var(--bg-timeline); }
  .main_timeline .timeline:nth-child(3n) .timeline_content,
  .main_timeline .timeline:nth-child(3n) .timeline_content:before,
  .main_timeline .timeline:nth-child(3n) .timeline_content:after,
  .main_timeline .timeline:nth-child(3n) .icon{ background: var(--bg-timeline); }
  .main_timeline .timeline:nth-child(3n) .icon{ box-shadow: 0 0 0 2px var(--bg-timeline); }
  .main_timeline .timeline:nth-child(4n) .timeline_content,
  .main_timeline .timeline:nth-child(4n) .timeline_content:before,
  .main_timeline .timeline:nth-child(4n) .timeline_content:after,
  .main_timeline .timeline:nth-child(4n) .icon{ background: var(--bg-timeline); }
  .main_timeline .timeline:nth-child(4n) .icon{ box-shadow: 0 0 0 2px var(--bg-timeline); }
  @media only screen and (max-width: 767px){
    .main_timeline:before{
        left: 0;
        right: auto;
    }
    .main_timeline .timeline,
    .main_timeline .timeline:nth-child(even),
    .main_timeline .timeline:nth-child(odd){
        width: 100%;
        float: none;
        padding: 20px 30px;
        margin: 0 0 30px 0;
        border-right: none;
        border-left: 7px solid var(--bg-root-timeline);
        border-radius: 30px 0 0 0;
        right: auto;
        left: 0;
    }
    .main_timeline .icon{
        left: auto;
        right: 0;
    }
  }
  @media only screen and (max-width: 480px){
    .main_timeline .title{ font-size: 18px; }
  }
  