.header_filter::before {
    background: rgba(0,0,0,.5);
}

.page_header {
    background-size: 100% 100% !important; 
    margin: 0;
    padding: 0;
    border: 0;
    display: flex;
    align-items: center;
}

.page_header, .header_filter:after, .header_filter:before {
    height: 70vh;
}

.page_header, .header_filter{
    clip-path: polygon(0 0, 100% 0, 100% 60%, 0% 100%);
}

.header_filter:after, .header_filter:before {
    content: "";
    position: absolute;
    width: 100%;
    display: block;
    left: 0;
    top: 0;
}

.profile_page .page_header {
    height: 380px;
    background-position: center;
    text-align: center;
}

@media screen and (min-width : 320px){
    .page_header, .header_filter {
        background-position: 25% 0% !important;
    }
}

