@import url(https://fonts.googleapis.com/css?family=Montserrat);

.svg {
    display: block;
    font: 0.8em 'Loading';
    width: 300px;
    margin: 0 auto;
}

.load{
    margin-top: 10%; 
}
.textCopy {
    fill: none;
    stroke: #F2385A;
    stroke-dasharray: 6% 29%;
    stroke-width: 5px;
    stroke-dashoffset: 0%;
    animation: stroke-offset 5.5s infinite linear;
}

.textCopy:nth-child(1){
	stroke: #F5A503;
	animation-delay: -1;
}

.textCopy:nth-child(2){
	stroke: #4D163D;
	animation-delay: -2s;
}

.textCopy:nth-child(3){
	stroke: #56D9CD;
	animation-delay: -3s;
}

.textCopy:nth-child(4){
	stroke: #BD0034;
	animation-delay: -4s;
}

.textCopy:nth-child(5){
	stroke: #3AA1BF;
	animation-delay: -5s;
}

@keyframes stroke-offset{
	100% {stroke-dashoffset: -35%;}
}