.MuiAppBar-colorPrimary{
    @include themify($themes) {
        background-color: themed('backgroundColor') !important;  
      }
}

//select component

.wrapper-select{
    width: 100% !important;
}

fieldset, fieldset:hover {
    @include themify($themes) {
        border-color: themed('textColor') !important;
    } 
}

.MuiSelect-selectMenu{
    padding-top: 10% !important;
    padding-bottom: 10% !important;
}

.MuiSelect-icon{
    @include themify($themes) {
        color: themed('textColor');
    } 
}

.MuiInput-underline:after {
    @include themify($themes) {
        border-bottom-color: themed('textColor');
    }
}

.MuiSelect-root {
    @include themify($themes) {
        color: themed('textColor');  
    } 
}

#select-label{
    @include themify($themes) {
        color: themed('textColor');  
    } 
}

.theme-light .app-bar {
    background-color: #000 !important;

    .MuiSelect-select{
        @extend .theme-dark;
    }
}

//Tabs
.MuiTabs-root, .MuiTab-root{
    @extend .theme;
}

.MuiFormLabel-root, .MuiFormLabel-root.Mui-focused, .MuiInputBase-input{
    @include themify($themes) {
        color: themed('textColor');  
    } 
}

.MuiTab-textColorPrimary.Mui-selected{
    @include themify($themes) {
        color: themed('textColor');  
        border-bottom-color: red !important;
    } 
}

.MuiTabs-indicator{
    @include themify($themes) {
        background-color: themed('textColor') !important;
    } 
}

@media screen and (max-width: 500px) {
    .MuiTab-wrapper{
        font-size: 10px;
    }
}

.MuiSnackbar-root{
    top: 80px !important;
}

.MuiRating-label{
    font-size: 250%;
}

.MuiButton-contained{
    @include themify($themes) {
        background-color: themed('secondary') !important;
    } 
}