$white: #fff;

:root{
  --bg-timeline: #3282b8;
  --bg-root-timeline: #084772;
}

$themes: (
  light: (
    backgroundColor: #fff,
    textColor: #000,
    buttonTextColor: #408bbd,
    secondary: #3282b8,
    buttonTextTransform: none,
    buttonTextHoverColor: #61b0e7,
    buttonColor: #fff,
    buttonBorder: 2px solid #fff,
    shadow: rgba(0,0,0,.14)
  ),
  dark: (
    backgroundColor: #222,
    secondary: #3282b8,
    textColor: #ddd,
    buttonTextColor: #aaa,
    buttonTextTransform: uppercase,
    buttonTextHoverColor: #ddd,
    buttonColor: #333,
    buttonBorder: 1px solid #aaa,
    shadow: #000
  ),
);